import React, { useEffect, useState, CSSProperties } from "react";
import "./App.css";
import Card from "@mui/material/Card";
import Box from "@mui/material/Box";
import Stack from "@mui/material/Stack";
import Divider from "@mui/material/Divider";
import Typography from "@mui/material/Typography";
import { Button, FormControlLabel, FormGroup, Switch, TextField } from "@mui/material";

// Type Definitions
type GreetingProps = {};

type Data = {
  cod: number;
  main: {
    temp: number;
    humidity: number;
    sea_level: number;
    temp_max: number;
    temp_min: number;
  };
  name: string;
  weather: {
    main: string;
    description: string;
    icon: string;
  }[];
  wind: {
    speed: number;
  };
};

const Greeting: React.FC<GreetingProps> = () => {
  const [data, setData] = useState<Data | null>(null);
  const [location, setLocation] = useState<string>("Ho Chi Minh City");
  const [isSubmit, setIsSubmit] = useState<boolean>(false);
  const [bg, setBg] = useState<boolean>(false);

  // CSS Styles
  const backgroundStyle: CSSProperties = {
    backgroundImage: bg
      ? "url(https://images.unsplash.com/photo-1565642899687-1c332fb7dc65?q=80&w=1770&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D)"
      : "url(https://images.unsplash.com/photo-1730534306787-59163ca22083?q=80&w=1770&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D)",
    backgroundPosition: "center center",
    height: "100vh",
  };
  const titleStyle: CSSProperties = {
    backgroundColor: bg ? "#FDF5E6" : "rgba(30, 30, 30, 1)",
    marginTop: "0",
    color: !bg ? "white" : "orange",
  };
  const switchContainerStyle: CSSProperties = {
    marginRight: "5px",
    float: "right",
    backgroundColor: bg ? "#FDF5E6" : "rgba(192, 192, 192, 1)",
    padding: "5px",
  };
  const listStyle: CSSProperties = {
    display: "inline-block",
    listStyleType: "none",
    backgroundColor: bg ? "rgba(255,255,255,0.9)" : "rgba(30,30,30,0.5)",
    borderRadius: "15px",
    boxShadow: "0 4px 8px rgba(0, 0, 0, 0.2)",
    padding: "20px",
    margin: "0px auto",
    textAlign: "center",
    minWidth: "360px",
    minHeight: "470px",
  };
  const cardStyle: CSSProperties = {
    maxWidth: 360,
    minHeight: "420px",
    backgroundColor: bg ? "rgba(255,255,255,0.5)" : "rgba(255,255,255,0.3)",
  };
  const cityNameStyle: CSSProperties = {
    marginBottom: "30%",
    color: bg ? "orange" : "white",
    fontWeight: "bold",
  };
  const weatherInfoStyle: CSSProperties = {
    fontSize: "24px",
    color: bg ? "orange" : "rgba(255,255,255,0.8)",
    fontWeight: "bold",
  };
  const textStyle: CSSProperties = {
    color: bg ? "text.secondary" : "white",
  };
  const inputStyle: CSSProperties = {
    backgroundColor: bg ? "rgba(255,255,255,1)" : "rgba(255,255,255,0.8)",
    margin: "8px",
    width: "350px",
    marginLeft: "6.5%",
  };
  const btnStyle: CSSProperties = {
    padding: "10px",
    margin: "15px 10px",
  };
  const spanLightStyle: CSSProperties = { color: "red" };
  const spanStyle: CSSProperties = { color: "lightblue" };

  // Fetch data
  useEffect(() => {
    const fetchAPI = async () => {
      try {
        const apiKey = "a638d72e1ded6bfebc1f457af80a00cc";
        const res = await fetch(
          `https://api.openweathermap.org/data/2.5/weather?q=${location}&appid=${apiKey}`
        );
        const dataRes: Data = await res.json();
        if (dataRes.cod !== 200) {
          alert("Invalid city name, resetting to Ho Chi Minh City");
          setLocation("Ho Chi Minh City");
          setIsSubmit(!isSubmit);
        } else {
          setData(dataRes);
        }
        setData(dataRes);
        console.info(dataRes);
      } catch (ex) {
        if (ex instanceof Error) {
          console.error(ex.message);
        } else {
          console.error("Unknown Error");
        }
      }
    };
    fetchAPI();
  }, [isSubmit]);

  const submitCityLocation = () => {
    setIsSubmit(!isSubmit);
  };

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setBg(event.target.checked);
    console.info("Switch is now: ", event.target.checked ? "ON" : "OFF");
  };

  return (
    <div style={backgroundStyle} className="App">
      <h1 style={titleStyle}>WEATHER APP</h1>
      <FormGroup style={switchContainerStyle}>
        <FormControlLabel
          control={<Switch color="warning" checked={bg} onChange={handleChange} />}
          label={!bg ? "Light" : "Night"}
          style={{ color: bg ? "coral" : "white", fontWeight: "bold" }}
        />
      </FormGroup>
      <div style={{ marginLeft: "7.5%" }}>
        {data === null ? (
          <p>Nothing Data To Show</p>
        ) : (
          <div>
            <ul style={listStyle}>
              <li style={weatherInfoStyle}>Weather Information</li>
              <li style={{ minWidth: "360px" }}>
                <Card variant="outlined" sx={cardStyle}>
                  <Box sx={{ p: 2 }}>
                    <Stack
                      direction="row"
                      sx={{ justifyContent: "space-between", alignItems: "center" }}
                    >
                      <Typography style={cityNameStyle} gutterBottom variant="h5">
                        {data.name || "N/A"}
                      </Typography>
                      <Typography gutterBottom variant="h5">
                        <p></p>
                        <span style={{ color: bg ? "orange" : "lightblue" }}>
                          {data?.weather?.[0]?.main || "N/A"}
                        </span>
                      </Typography>
                      <img
                        width={80}
                        height={80}
                        src={`http://openweathermap.org/img/wn/${data?.weather?.[0].icon}.png`}
                        alt={data?.weather?.[0].description}
                        className="weather-icon"
                      />
                    </Stack>
                    <div style={{ textAlign: "left" }}>
                      <Typography variant="body2" sx={textStyle}>
                        Temperature: <span style={!bg ? spanStyle : spanLightStyle}>{data.main?.temp || "N/A"} °F</span>
                        <p></p>
                        Min Temp: <span style={!bg ? spanStyle : spanLightStyle}>{data.main?.temp_min || "N/A"} °F</span>
                        <p></p>
                        Max Temp: <span style={!bg ? spanStyle : spanLightStyle}>{data.main?.temp_max || "N/A"} °F</span>
                        <p></p>
                        Wind Speed: <span style={!bg ? spanStyle : spanLightStyle}>{data.wind?.speed || "N/A"} Km/h</span>
                        <p></p>
                        Humidity: <span style={!bg ? spanStyle : spanLightStyle}>{data.main?.humidity || "N/A"} %</span>
                        <p></p>
                        Description: <span style={!bg ? spanStyle : spanLightStyle}>{data?.weather?.[0]?.description || "N/A"}</span>
                      </Typography>
                    </div>
                  </Box>
                  <Divider />
                </Card>
              </li>
            </ul>
          </div>
        )}
      </div>
      <div>
        <TextField
          style={inputStyle}
          variant="filled"
          label="Location"
          value={location}
          onChange={(t) => setLocation(t.target.value)}
          defaultValue="Ho Chi Minh City"
        />
        <Button style={btnStyle} onClick={submitCityLocation} variant="contained" color={bg ? "warning" : "info"}>
          Check
        </Button>
      </div>
    </div>
  );
};

export default Greeting;
